import { Brand, GetRewardDataRuntime, RewardRegistryData, RewardRuntime } from "@me-protocol/interfaces";

export function getBrandsInRewardArray (rewards: GetRewardDataRuntime["rewards"]) {

    const val: Brand[] = []

    rewards.forEach((r)=> {

        if (!(val.find((e)=> e.id === r.brand.id)) ) {
            val.push(r.brand)
        }
    })
    return val;
}

export function filterRewardsByBrandId(rewards: GetRewardDataRuntime['rewards'], brandId?: string | null) {
    if (!brandId) {
        return rewards;
    }
    let val = rewards.filter((r)=> r.brand.id === brandId )
    return val;
}

export function filterTransactionHistoryByBrandId(transactionHistoryByBrand: Record<string, RewardRegistryData[]>, brandId?: string | null) {
    if (!brandId) {
        return transactionHistoryByBrand;
    }
    return {
        [brandId]: transactionHistoryByBrand[brandId]
    }
}

export function getRewardsByBrandId (rewards: GetRewardDataRuntime["rewards"], brandId: string) {

    const val: RewardRuntime[] = []
    rewards.forEach((r)=> {

        if (r.brand.id === brandId) {
            val.push(r)
        }
    })

    return val;

}