import { GetRewardData, GetRewardDataRuntime } from "@me-protocol/interfaces";

export function getPercentageDistribution(
  data: Array<{brand: string, [x: string]: any}>,
  percentageField?: string
){
  let total = 0;
  let percentageFieldVal = percentageField || "percentage";
  data.forEach((item) => {
    total += item[percentageFieldVal];
  });
  data.forEach((item) => {
    // round to nearest 1 dp
    item[percentageFieldVal] =
      Math.round((item[percentageFieldVal] / total) * 1000) / 10;
  });
  return data;
}


export function getBrandPercentageDistribution(
  rewards: GetRewardData["rewards"]
) {
  const dataArray: { brand: string; amount: number }[] = [];
  // Get the quantity of every brand of reward and store in array of objects {brand, amount}
  rewards.forEach((reward) => {
    const brandExists = dataArray.find(
      (item) => item.brand === reward.brand.name
    );
    if (brandExists) {
      const matchedBrandIndex = dataArray.findIndex(
        (item) => item.brand === reward.brand.name
      );
      const previousData = dataArray[matchedBrandIndex];
      dataArray[matchedBrandIndex] = {
        ...previousData,
        amount: previousData.amount + 1,
      };
      // Get the percentage distribution by amounts
    } else {
      dataArray.push({ brand: reward.brand.name, amount: 1 });
    }
  });
  console.log(getPercentageDistribution(dataArray))
  return getPercentageDistribution(dataArray, "amount");
}


export function getRewardsPercentageDistribution(
  rewards: GetRewardDataRuntime["rewards"]
) {
  const dataArray: { brand: string; amount: number }[] = [];
  // Get the quantity of every brand of reward and store in array of objects {brand, amount}
  rewards.forEach((reward) => {
    const brandExists = dataArray.find(
      (item) => item.brand === reward.brand.name
    );
    if (brandExists) {
      const matchedBrandIndex = dataArray.findIndex(
        (item) => item.brand === reward.brand.name
      );
      const previousData = dataArray[matchedBrandIndex];
      dataArray[matchedBrandIndex] = {
        ...previousData,
        amount: previousData.amount + parseFloat(reward.balance.balance),
      };
      // Get the percentage distribution by amounts
    } else {
      dataArray.push({ brand: reward.brand.name, amount: parseFloat(reward.balance.balance) });
    }
  });
  console.log(getPercentageDistribution(dataArray))
  return getPercentageDistribution(dataArray, "amount");
}

