import { RewardRegistryData } from "@me-protocol/interfaces";

export function getTransactionHistoryByBrand(rewardRegistry: RewardRegistryData[]) {
console.log(rewardRegistry)
  const result: Record<string, RewardRegistryData[]> = {};

  rewardRegistry.forEach((item) => {
    let matchedResult = result[item?.rewardRegistry?.reward?.brandId];
    if (matchedResult) {
      result[item?.rewardRegistry?.reward?.brandId] = [...matchedResult, item];
    } else {
      result[item?.rewardRegistry?.reward?.brandId] = [item];
    }
  });

  console.log("res", result)

  return result;
}
