import {
  Stack,
  Flex,
  Heading,
  Center,
  Icon,
  Spinner,
  Select,
  Input,
  HStack,
  PinInput,
  PinInputField,
  VStack,
  Image,
  Text,
  Box,
} from "@chakra-ui/react";
import { nanoid } from "@reduxjs/toolkit";
import Lottie from "lottie-react";
import React, { FC, Suspense } from "react";
import { IoMdCheckmark } from "react-icons/io";
import MeProtocolButton from "../../../components/MeProtocolButton";
import Images_Icons from "../../../constants/icons-images";
import { useMyAssetsStyles } from "./DashboardMyAssets";
import checkMark from "../../../assets/json/checkMark.json";

type SyncModalCasesProps = {
  brandToSync: any;
  onClose3: any;
  syncModalPage: any;
  setSyncModalPage: any;
  brandSyncLoading: any;
  verificationOption: any;
  setVerificationOption: any;
  emailRef: any;
  codeErrMsg: any;
  setCodeErrMsg: any;
  setVerifyEmailOtpSent: any;
  verificationPhone: any;
  verifyEmailOtpSent: any;
  emailCodeLoading: any;
  setVerificationPhone: any;
  SendEmailVerificationForSync: any;
  SendEmailCodeForSync: any;
  setEmailCode: any;
};

const SyncModalCases: FC<SyncModalCasesProps> = ({
  brandToSync,
  onClose3,
  syncModalPage,
  setSyncModalPage,
  brandSyncLoading,
  verificationOption,
  setVerificationOption,
  emailRef,
  codeErrMsg,
  setCodeErrMsg,
  setVerifyEmailOtpSent,
  verificationPhone,
  verifyEmailOtpSent,
  emailCodeLoading,
  setVerificationPhone,
  SendEmailVerificationForSync,
  SendEmailCodeForSync,
  setEmailCode,
}): JSX.Element => {
  const { head, desc, search } = useMyAssetsStyles();

  const Modals = () => {
    switch (syncModalPage) {
      case 0:
        return (
          <Stack>
            <Flex flexDir={"row"} justify="space-between">
              <Heading {...head}>Sync {brandToSync?.name} Rewards</Heading>
              <Center as="button" onClick={onClose3}>
                <Image w="30px" h="30px" src={Images_Icons.cancel_icon} />
              </Center>
            </Flex>
            <Flex
              flexDir={"row"}
              align={"center"}
              justify={"space-between"}
              py={"2%"}
            >
              <Center w="100px" h="100px" overflow={"hidden"}>
                <Image
                  borderRadius={"10px"}
                  objectFit="contain"
                  src={brandToSync?.rewardImage}
                />
                <Box
                  w="100px"
                  h="100px"
                  borderRadius={"100px"}
                  pos="absolute"
                  bg="#9999992D"
                />
              </Center>

              {/* <Avatar size={"xl"} /> */}
              <Text {...desc} noOfLines={4} w="100%" pl="3%">
                {brandToSync?.description}
              </Text>
            </Flex>
            <Heading {...head}>What youâ€™ll get</Heading>
            <Flex
              flexDir={"column"}
              bg={"#f6f6f6"}
              p={"5%"}
              borderRadius={"15px"}
            >
              {[
                `Authorize Me app to access your reward balance with ${brandToSync?.brandName}`,
                `Me app will Read reward balance from ${brandToSync?.brandName}`,
                `Me app will monitor changes in reward balance `,
              ]?.map((e: any) => (
                <Flex key={nanoid()}>
                  <Center
                    bg={"#000000"}
                    w={"20px"}
                    h={"20px"}
                    borderRadius={"20px"}
                  >
                    <Icon as={IoMdCheckmark} color={"light"} />
                  </Center>
                  <Text {...desc} noOfLines={2} pl={"2%"} w={"100%"}>
                    {e}
                  </Text>
                </Flex>
              ))}
            </Flex>
            <Suspense fallback={<Spinner size="sm" />}>
              <MeProtocolButton
                isLoading={brandSyncLoading}
                onClick={() => setSyncModalPage(1)}
                // onClick={() => SyncMyReward(brandToSync)}
                name={`Connect with ${brandToSync?.rewardName || "reward"}`}
              />
            </Suspense>
          </Stack>
        );
      case 1:
        return (
          <Stack>
            <Flex flexDir={"row"} justify="space-between">
              <Heading {...head}>Verify identity </Heading>
              <Center
                as="button"
                onClick={() => {
                  setSyncModalPage(0);
                  onClose3();
                  setVerificationOption("");
                  setVerifyEmailOtpSent(false);
                  setCodeErrMsg("");
                }}
              >
                <Image src={Images_Icons.cancel_icon} w="30px" h="30px" />
              </Center>
            </Flex>
            <Text {...desc} w="100%" pt="1%">
              Please select your preferred means of identification from the
              available options below
            </Text>
            <Flex {...search}>
              <Select
                color={"dark"}
                onChange={({ target }: any) => {
                  setVerificationOption(target.value);
                }}
                value={verificationOption}
                variant="unstyled"
                placeholder="Select verification option"
                size={"md"}
                _placeholder={{ color: "#999999" }}
              >
                {["Email", "Phone number"]?.map((e) => (
                  <option key={nanoid()} value={e}>
                    {e}
                  </option>
                ))}
              </Select>
            </Flex>
            {verificationOption && (
              <Stack>
                {verificationOption === "Email" ? (
                  <Input
                    {...search}
                    // value={emailRef.current}
                    ref={emailRef}
                    // onChange={(e: any) => {
                    // setemailRef.current(e?.target.value);
                    // }}
                    placeholder="Enter email address"
                  />
                ) : (
                  <Input
                    {...search}
                    value={verificationPhone}
                    onChange={(e: any) => {
                      setVerificationPhone(e?.target.value);
                    }}
                    placeholder="Enter phone number"
                  />
                )}
              </Stack>
            )}
            {verifyEmailOtpSent && (
              <Box my={"3%"}>
                <Text fontSize={"10px"}>
                  Please provide the OTP sent to your inbox
                </Text>
                <HStack w={"100%"} h={"60px"}>
                  <PinInput
                    onComplete={(e) => {
                      setEmailCode(e);
                      setCodeErrMsg("");
                    }}
                    otp
                    placeholder={"*"}
                  >
                    {[1, 2, 3, 4, 5, 6]?.map(() => (
                      <PinInputField
                        key={nanoid()}
                        bg={"grey"}
                        borderRadius={10}
                        borderWidth={0}
                        w={"15%"}
                        h={"100%"}
                      />
                    ))}
                  </PinInput>
                </HStack>
              </Box>
            )}
            {codeErrMsg && (
              <Text {...desc} color={"red"}>
                {codeErrMsg}
              </Text>
            )}
            <Suspense fallback={<Spinner size="sm" />}>
              <MeProtocolButton
                isLoading={emailCodeLoading || brandSyncLoading}
                onClick={() => {
                  if (verifyEmailOtpSent) {
                    SendEmailVerificationForSync();
                    return;
                  }
                  SendEmailCodeForSync();
                }}
                // isDisabled={!emailRef.current.includes("@")}
                name={"Continue"}
              />
            </Suspense>
          </Stack>
        );
      case 2:
        return (
          <Stack w="100%">
            <VStack>
              <Lottie
                style={{ width: "150px", height: "150px" }}
                animationData={checkMark}
                loop={false}
                autoplay
              />
              <Heading fontSize={"18px"} pb="10px">
                Brand connected successfully
              </Heading>
              <Suspense fallback={<Spinner size="sm" />}>
                <MeProtocolButton
                  name="Ok"
                  onClick={() => {
                    // refetchSyncRewards();
                    setSyncModalPage(0);
                    onClose3();
                    setVerificationOption("");
                    setVerifyEmailOtpSent(false);
                    setCodeErrMsg("");
                    setTimeout(() => {
                      window.scrollTo({ top: 0, behavior: "smooth" });
                    }, 100);
                  }}
                />
              </Suspense>
            </VStack>
          </Stack>
        );
    }
  };

  return <>{Modals()}</>;
};

export default SyncModalCases;
